function FooterComponent(){ 
      if(App.state.config.is_same(config_atima13)){
          App.info = "setting: ATIMA v1.3";
      }
      else if(App.state.config.is_same(config_atima14)){
          App.info = "setting: ATIMA v1.4";
      }
      else{
          App.info ="setting: custom";
      }
  
      return m("footer",{class:styles.footer},
              m("span",{style:"color:#666",class:"f7"},App.info));
  }

function NavigationComponent(l){
    return m("nav",{class:styles.nav},l);
  }
  
  
  var NavigationList_Main = function(){
    return [
      m("a",{class:styles.nav_item,onclick:function(){
            SaveFile(App.state,"webatima_input.json");}
            },"Save"),
      FileLoader({class:styles.nav_item,title:"Load",id:"apploader"},App.LoadJSONFile),
      m("a",{class:styles.nav_item_right+" fr",href:"about.html",target:"_blank"},"About"),
      m("a",{class:styles.nav_item_right+" fr ",onclick:function(){m.route.set("/config");}},[m.trust(svg_settings()),"Settings"]),
    ];                                                             
  };                                                               
                                                                   
  var NavigationList_NucChange = function(){                       
    return [                                                       
      m("a",{class:styles.nav_item,onclick:function(){m.route.set("/");}},"Back"),
    ];                                                             
  };                                                               
                                                                   
  var NavigationList_Results = function(){                         
    return [                                                       
      m("a",{class:styles.nav_item,onclick:function(){m.route.set("/");}},"Back"),
      m("a",{class:styles.nav_item,onclick:function(){m.route.set("/nucchange");}},"Projectile"),    
      m("a",{class:styles.nav_item_right,href:"about.html",target:"_blank"},"About"),
    ];
  };

var MainPage = {};
MainPage.oninit = function(){
};

MainPage.view = function(vnode){
  return [m("div",{class:styles.container_narrow},[
    NavigationComponent(NavigationList_Main()),
    m("div",{class:styles.combine([styles.div_row,styles.padding_vertical])},[
            m("div",{class:styles.div_cell_20a},m(CardComponent,{title:"Projectile",content:m(NucleusInfo,{"nucleus":App.projectile})})),
            m("div",{class:styles.div_cell_80},m(CardComponent,{title:"Matter",content:m(MatterEditor)})),
        ]),
    m("div",{class:styles.combine([styles.div_row,styles.padding_vertical])},
        m("div",{class:styles.div_cell_full},[
                m("h2","Results"),
                m("a",{class:styles.button_full,
                            onclick:function(){
                                if(MatterEditor.vm.editindex == -1)
                                    m.route.set("/results");}
                            },"Calculate")
                //AtimaResults
        ])
    )
  ]),
    FooterComponent()];
};

var NucChangePage = {};
NucChangePage.view = function(vnode){
  return m("div",{class:styles.container_narrow},[
    NavigationComponent(NavigationList_NucChange()),
    m(NucleusSelector)
  ]);
};

var ResultsPage = {};
ResultsPage.view = function(vnode){
    if(!App.loaded){
        m.route.set("/");
        return; 
        }
    return [
            m("div",{class:styles.container},[
                NavigationComponent(NavigationList_Results()),
                m(AtimaResultsView),
            ]),
            FooterComponent()
    ];
};

var PlotPage = {};
PlotPage.oninit = function(vnode){
    vnode.state.plotdata = PlotData;
};

PlotPage.oncreate = function(vnode){
    vnode.state.plotdata.reset();
    Catima.get_data(Catima.prepare(true)).then(function(res){
        if(res.length>0){
            vnode.state.plotdata.sort(res);
            m.redraw();
            vnode.state.plotdata.update();
        }
        else{
            vnode.state.plotdata.status = "failed";
        }
    }).catch(function(e){
        App.report(e);
    });
};

PlotPage.view = function(vnode){
    if(!App.loaded){
        m.route.set("/");
        return; 
        }
    return m("div",{class:styles.container},[
        NavigationComponent(NavigationList_NucChange()),
        m(PlotComponent,vnode.state.plotdata)
    ]);
    
};

var MaterialPlotPage = {};
MaterialPlotPage.oninit = function(vnode){
    vnode.state.plotdata = MaterialPlotData;
};

MaterialPlotPage.oncreate = function(vnode){
    vnode.state.plotdata.reset();
    var index = App.get_editindex();
    if(index<0)return;
    Catima.get_data(Catima.prepare_one(index)).then(function(res){
        if(res.length>0){
            vnode.state.plotdata.sort(res);
            m.redraw();
            vnode.state.plotdata.update();
        }
        else{
            vnode.state.plotdata.status = "failed";
        }
    }).catch(function(e){
        App.report(e);
    });
};

MaterialPlotPage.view = function(vnode){
    if(!App.loaded){
        m.route.set("/");
        return; 
        }
    return m("div",{class:styles.container},[
        NavigationComponent(NavigationList_NucChange()),
        m(PlotComponent,vnode.state.plotdata)
    ]);
};

var ConfigPage = {};
ConfigPage.view = function(vnode){
    return m("div",{class:styles.container_narrow},[
        NavigationComponent(NavigationList_NucChange()),
        m(ConfigComponent)
    ]);
};

//// initialization //////
Raven.context(function(){

main_element.innerHTML = loader("loading");
//main_element.innerHTMaL();
var ss = window.location.search;
var idx = ss.indexOf("?c=");
if(ss.length>3 && idx>-1){
    var len = ss.length;
    if(ss.charAt(len-1)=="/"){
        len = len - 1;
    }
    var uid = ss.substring(idx+3, len);
    App.load_config(uid);
}
else{
    var stored = StorageAPI.load("state");
    App.Load(stored);
}

m.route(document.getElementById('NS'),"/",{
  "/":MainPage ,
  "/nucchange":NucChangePage,
  "/results":ResultsPage,
  "/plot":PlotPage,
  "/materialplot":MaterialPlotPage,
  "/config":ConfigPage
});


});