import {MatterEditor} from "./material_editor_component.js";
import {styles} from "./styles.js";
import {badge_error, button} from "./utils.js";
import {Catima} from "./catima.js";
import {CardComponent} from "./components.js";
import {App} from "./app.js";
import {Modal} from "./modal.js";

var AtimaResults = {};
AtimaResults.result = {};
AtimaResults.matter_names = [];
AtimaResults.done = true;

AtimaResults.prepare = function(){
  var matter = MatterEditor.vm.layers;
  AtimaResults.matter = MatterEditor.vm.layers;
  for(var i=0;i<matter.length;i++){
    //if(matter[i].rho > 0 && matter[i].th > 0){
        AtimaResults.matter_names.push(matter[i].name);
    //    }
    }

};

AtimaResults.prepareoutput = function(res){
    if(res.result){
        res.result.sigma_a = res.result.sigma_a*1000.0; //convert to mrad
    }
    if(res.partial){
        for(var e in res.partial){
            res.partial[e].sigma_a = res.partial[e].sigma_a*1000.0;
        }
    }
    //return res;
};

AtimaResults.getResult = function(){
        if(App.datavalid && App.result && AtimaResults.done && !App.result.error){
            return;
        }
        AtimaResults.result = {}; // reset old values
        AtimaResults.matter_names = [];
        AtimaResults.done = false;

        AtimaResults.prepare();
        var input = Catima.prepare();
        Catima.calculate(input).then(
              function(res){
                  if(!res.result||res.error){
                    AtimaResults.done = true;
                    App.clear_cache();
                    AtimaResults.result = {error:"Failed to retrieve data from Atima Server"};       
                    m.redraw();
                    return;
                    }
                  AtimaResults.done = true;
                  App.datavalid = true;
                  AtimaResults.prepareoutput(res);
                  AtimaResults.result = res;
                  if(res.result){
                    AtimaResults.result.result.Brho = NucKinematics.brho_from_T(res.result.Eout,res.projectile.A,res.projectile.Z).toPrecision(6);
                    AtimaResults.result.result.Beta = NucKinematics.beta_from_T(res.result.Eout).toPrecision(6);
                    }
                   App.result = AtimaResults.result;
                   m.redraw();                  
              },
              function(){
                AtimaResults.done = true;
                App.clear_cache();
                AtimaResults.result = {error:"Failed to retrieve data from Atima Server"};
                App.report("Failed to retrieve data from Atima Server");
                m.redraw();
            }
              ).catch(function(e){
                  App.report(e);
              });
};


var AtimaResultsView = {};
AtimaResultsView.oninit = function(){
    AtimaResults.getResult();
};

AtimaResultsView.TotalTemplate = function(){
    var tostring = function(val){
        if(val<0.01){
            return val.toExponential(4);
        }
        else{
            return val.toFixed(4);
            }
    };
    var res = AtimaResults.result;
    if(!res.result) return null;
    
    return m("div",{class:styles.div_row},[
                m("div",{class:styles.div_cell_100},[
                        m("p","Projectile: ", App.projectile.state.Symbol),
                        ]),
                m("div",{class:styles.div_cell_auto},[
                        m("p",m.trust("E<sub>in</sub>")," = ",tostring(res.result.Ein)," MeV/u"),
                        m("p",m.trust("E<sub>out</sub>")," = ",res.result.Eout.toFixed(4)," MeV/u"),
                        m("p",m.trust("E<sub>loss</sub>")," = ",tostring(res.result.Eloss)," MeV"),
                        ]),
                        
                m("div",{class:styles.div_cell_auto},[
                        m("p",m.trust("B&#961")," = ",res.result.Brho," Tm"),
                        m("p",m.trust("&#946")," = ",res.result.Beta),
                        m("p",m.trust("TOF")," = ",res.result.tof.toFixed(6)," ns")
                        ]),
                m("div",{class:styles.div_cell_auto},[
                        m("p",m.trust("&#963<sub>E</sub>")," = ",tostring(res.result.sigma_E)," MeV/u"),
                        (res.result.Eout>0)?m("p",m.trust("&#963<sub>p</sub>/p = "),(NucKinematics.relative_momentum_deviation(res.result.sigma_E,res.result.Eout)).toExponential(3)):null,
                        m("p",m.trust("&#963<sub>a</sub>")," = ",tostring(res.result.sigma_a)," mrad")
                        ])
                
            ]);    
    
};


AtimaResultsView.PartialTemplate = function(){
    var tostring = function(val){
        if(val<0.01){
            return val.toExponential(3);
            }
        else{
            return val.toFixed(3);
        }
    };
    
    var res = AtimaResults.result;
    if(!res.partial) return null;
    return  m("div",{style:"overflow-x:auto;"},[
                m("table",{class:styles.table},[
                m("thead",m("tr",{class:styles.thead_tr},
                    [
                  m("th",{class:styles.th},"#"),
                  m("th",{class:styles.th},"name"),
                  m("th",{class:styles.th},m.trust("E<sub>in</sub><br>(MeV/u)")),
                  m("th",{class:styles.th},m.trust("E<sub>out</sub><br>(MeV/u)")),
                  m("th",{class:styles.th},m.trust("E<sub>loss</sub><br>(MeV)")),
                  m("th",{class:styles.th},m.trust("&#963<sub>E</sub><br>(MeV/u)")),
                  m("th",{class:styles.th},m.trust("&#963<sub>a</sub><br>(mrad)")),
                  m("th",{class:styles.th},m.trust("range<br>(g/cm<sup>2</sup>)")),
                  m("th",{class:styles.th},m.trust("&#963<sub>r</sub><br>(g/cm<sup>2</sup>)")),
                  m("th",{class:styles.th},m.trust("dE<sub>in</sub>/dx<br>(MeV/(g/cm<sup>2</sup>))")),
                  m("th",{class:styles.th},m.trust("tof<br>(ns)")),
                  m("th",{class:styles.th},m.trust("d/R")),
                  m("th",{class:styles.th},m.trust("W<sub>e</sub>")),
                  m("th",{class:styles.th},m.trust("W<sub>p</sub>"))
                ])),
                m("tbody",
                  res.partial.map(function(item,index){
                    var brho = NucKinematics.brho_from_T(item.Eout,res.projectile.A,res.projectile.Z).toPrecision(6) + "  Tm";
                    var beta = NucKinematics.beta_from_T(item.Eout).toPrecision(6);
                    var eouttitle = ("Brho = "+brho+"\nBeta = "+beta);
                    var matterial_title = ("material: "+AtimaResults.matter[index].symbol
                                            +"\nl = "+AtimaResults.matter[index].l + " cm"
                                            +"\nth. = "+AtimaResults.matter[index].th + " g/cm2"
                                            +"\ndensity = "+AtimaResults.matter[index].rho + " g/cm3"
                                        );
                    
                    return m("tr",{class:styles.tr},[
                              m("td",{class:styles.td},index),
                              m("td",{class:styles.td, title:matterial_title},AtimaResults.matter[index].name),
                              m("td",{class:styles.td},tostring(item.Ein)),
                              m("td",{class:styles.td,title:eouttitle},tostring(item.Eout)),
                              m("td",{class:styles.td},tostring(item.Eloss)),
                              m("td",{class:styles.td},tostring(item.sigma_E)),
                              m("td",{class:styles.td},tostring(item.sigma_a)),
                              m("td",{class:styles.td},tostring(item.range)),
                              m("td",{class:styles.td},tostring(item.sigma_r)),
                              m("td",{class:styles.td},tostring(item.dEdxi)),
                              m("td",{class:styles.td},tostring(item.tof)),
                              m("td",{class:styles.td},(item.range>0)?tostring(0.001*AtimaResults.matter[index].th/item.range):""),
                              m("td",{class:styles.td},(item.we && item.we>0)?tostring(item.we):""),
                              m("td",{class:styles.td},(item.wp && item.wp>0)?tostring(item.wp):"")
                    ]);
                  })
                )
              ])
    ]);    
};

AtimaResultsView.view = function(){
    var res = AtimaResults.result;
    
    if(!AtimaResults.done)return m("span","Loading...");
    if(res.error)return badge_error("Error: "+res.error);
    if(!res.result)return badge_error("Error: Could not find results");
    
    return m("div",[
            m("div",{class:styles.h3},"Results"),
            m("div",{class:styles.div_cell_full},m(CardComponent,{title:"Total Results",content:AtimaResultsView.TotalTemplate()})),
            m("div",{class:styles.div_cell_full},m(CardComponent,{title:"Partial Results",content:AtimaResultsView.PartialTemplate()})),
            (res.id)?
                m("div",{class:styles.div_cell_full},button("Share Link", function(){
                    var share_link = window.location.origin+window.location.pathname+"?c="+res.id;
                    var content = '<div><label>Link:<input class="w-100" type="text" value="'+share_link+'"></input></label></div>';
                    Modal.set_content(content);
                    Modal.open();
            })):null
  ]);
  
};

export {AtimaResultsView};
