    return {
        map : mocadi_matter_map,
        Get : function(i){
                if(i<200 || i>344){
                    return {};
                    }
                return mocadi_matter_map[i.toString()];
                },
        GetName: function(i){
                 var data = mocadi_matter_map[i];
                 if(!data.hasOwnProperty("key"))return "";
                 if(data.hasOwnProperty("name")){
                    if(data.key.indexOf("_")==-1){
                        return data.name + " - " + data.key;
                    }
                    else{
                       return data.name;
                    }
                 }
                 return data.key;
        },
        GetZ: function(s){
            for(var entry in mocadi_matter_map){
                if(mocadi_matter_map[entry].hasOwnProperty("name")){
                    if(mocadi_matter_map[entry].name.toLowerCase() == s.toLowerCase()){
                        return parseInt(entry);
                    }
                }
                if(mocadi_matter_map[entry].key.toLowerCase() == s.toLowerCase()){
			return parseInt(entry);
                }
            }
            return 0.0;
                }
        
    };


